import React, { useContext, useEffect } from "react"
import { PageProps, navigate } from "gatsby"
import { Router } from "@reach/router"
import { StyledFirebaseAuth } from "react-firebaseui"
import firebase from "firebase"
import { useSelector, useDispatch } from "react-redux"
import _ from "lodash"

import { setUser } from "../components/store/userReducer"
import { FirebaseContext } from "../components/firebase"
import Upload from "../containers/uploadPage/upload"
import Protectedroute from "../hoc/ProtectedRoute"
import { FBInstanceType } from "../components/firebase/firebase"

const UploadPage: React.FC<PageProps> = ({ data }) => {
  const stateUser = useSelector(state => state);
  const dispatch = useDispatch();

  useEffect(() => {
    const unsubscribe = fb.auth.onAuthStateChanged(user => {
      console.log(stateUser)

      if (user) {
        if (_.isEmpty(stateUser.default)) {
          dispatch(
            setUser({
              name: user.displayName,
              email: user.email,
              emailverified: user.emailVerified,
              photoURL: user.photoURL,
            })
          )
          console.log(stateUser);
          navigate("/upload");
        }
        console.log(user)
      }
    })
    return () => {
      unsubscribe()
    }
  })

  const uiConfig = {
    signInFlow: "popup",
    signInSuccessUrl: "/upload",
    signInOptions: [
      firebase.auth.EmailAuthProvider.PROVIDER_ID,
      firebase.auth.GoogleAuthProvider.PROVIDER_ID,
    ],
  }
  const fb: FBInstanceType = useContext(FirebaseContext)

  return (
    <Router basepath="/upload">
      <Protectedroute path="/" component={Upload} />
      <StyledFirebaseAuth
        path="/login"
        uiConfig={uiConfig}
        firebaseAuth={fb.auth}
      />
    </Router>
  )
}

export default UploadPage;
